@charset "utf-8";

@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap);

*{margin: 0px; padding: 0px; box-sizing: border-box;}
body{
  font-family: "Noto Sans", sans-serif;
  font-size: 13px; color: #333;
  background-color: #eff3f7;
}
a{
  color: #333;  font-size: 16px;
  text-decoration: none;
}
ul li{list-style: none;}

/* nav서식 */
nav{padding-top: 70px; text-align: center;}
.nav li{line-height: 40px;}
.nav a{
  text-decoration: none;
  color: #5d5d5d; font-size: 16px;
  padding: 5px 10px;
  text-shadow: 0p 1px 1px #adadad;
}

/* header 서식 */
.header{
  width: 200px; height: 100%;
  text-align: center;
  position: fixed; top: 0px; left: 0px;
  z-index: 100;
}
h1{padding: 70px 0px 50px 0px;}
.navi{line-height: 40px;}
.navi a{text-transform: uppercase; font-weight: bold;}
.navi a:hover{text-decoration: underline;}

/* main서식 */
main{
  width: calc(100% - 200px); height: 100%;
  position: fixed; top: 0px; left: 200px;
  padding-top: 50px; overflow: scroll;
}

/* footer 서식 */
.footer{
  width: 100%; background: #333;
  line-height: 40px;
  position: fixed; bottom: 0px; 
}
.footer address{
  color: #fff; text-align: center;
  font-style: normal;
}

